<template>
  <div class="home" >
    <Row style="margin-top: 16px;opacity: 0.8">
      <Col :span="3" :offset="20">
        <span v-if="address === ''">
          <Button shape="circle" @click="connectWallet">Connect Wallet</Button>
        </span>
        <span v-else>
          <Button shape="circle">connected</Button>
        </span>
      </Col>
    </Row>
    <Row>
      <Col :span="18" :offset="3">
        <p class="fontstyle">
          You have to have at least four piles of Rubbish in your wallet to have
          a Rubbish Beast
        </p>
      </Col>
    </Row>
    <Row>
      <Col :span="12" :offset="5">
        <p class="fontstyle">!!!You may need to follow the steps below to mint</p>
      </Col>
    </Row>
    <Row class="row">
      <Col :span="12" :offset="6">
        <p class="btnstyle">Step One: Get You Address Info~~</p>
      </Col>
    </Row>
    <Row class="row">
      <Col :span="12" :offset="6">
        <Button @click="initContracts">GetYourInfo</Button>
      </Col>
    </Row>
    <Row class="row">
      <Col :span="12" :offset="6">
        <p class="btnstyle">Step Two:Authorize Nft In Your Wallet~~</p>
      </Col>
    </Row>
    <Row class="row">
      <Col :span="12" :offset="6">
        <Button @click="approvelAll">ApprovelAll</Button>
      </Col>
    </Row>
    <Row class="row">
      <Col :span="12" :offset="6">
        <p class="btnstyle">Step 3wo:Mint The Beast Nft!!!</p>
      </Col>
    </Row>
    <Row class="row">
      <Col :span="12" :offset="6">
        <Button Button @click="rubbishClaim">ClaimBeast</Button>
      </Col>
    </Row>
  </div>
</template>

<script>
import { ethers } from "ethers";
import Web3Modal from "web3modal";
import { rubbishNftAbi, rubbishBeastAbi } from "../assets/abi1";

export default {
  data() {
    return {
      // home: {
      //   backgroundImage: "url(" + require("../assets/background.jpg") + ")",
      //   backgroundRepeat: "no-repeat",
      //   backgroundPosition: "center",
      //   width: "100%",
      //   height: "100%",
      //   backgroundSize: "100% 100%",
      //   position: "fixed",
      // },
      web3: "",
      rubbishContract: "",
      beastContract: "",
      // rubbishAddress: "0xa8d88f263bcf4e762f18c5b8d28ef6ef735a56ba",
      // beastAddress: "0x8a423B3135d13Db96279D2aB073Ff8E3474d24ea",
      rubbishAddress: "0x8b44b715004020773e8da1cd730de2f47c7d88b8",
      beastAddress: "0xc1AeA99329A439e30d622210c2c0782797C98C48",
      address: "",
      tokenIdArr: [],
      privateKey: "",
    };
  },
  methods: {
    connectWallet() {
      if (window.ethereum) {
        window.ethereum.enable().then((res) => {
          this.web3 = new this.Web3(window.web3.currentProvider);
          this.web3.eth.getAccounts().then((res) => {
            this.address = res[0];
          });
        });
      } else {
        alert("Please Install MetaMask!!!");
      }
    },
    async initContracts() {
      this.rubbishContract = await new this.web3.eth.Contract(
        rubbishNftAbi,
        this.rubbishAddress
      );
      this.beastContract = await new this.web3.eth.Contract(
        rubbishBeastAbi,
        this.beastAddress
      );
      let res = await this.rubbishContract.methods
        .tokensOfOwner(this.address)
        .call();
      this.tokenIdArr = res;
      this.$Modal.info({
        title: "Info",
        content: "get info successfully!",
        okText: 'confirm'
      })
    },
    async approvelAll() {
      let data = await this.rubbishContract.methods
        .setApprovalForAll(this.beastAddress, true)
        .encodeABI();
      let txDataA = {
        // nonce: web3.utils.toHex(nonce),//钱包地址的nonce
        to: this.rubbishAddress, //这笔交易的接收地址，为router合约
        from: this.address, //交易的发起地址
        data: data, //编码过后的交易数据
        // gas: 500000
        // value:amoutA,//携带的ETH数量
      };
      // this.web3.eth.signTransaction(txDataA,this.address,(err,res)=>{
      //   console.log(res)
      //   console.log(err)
      // })
      let res = this.web3.eth.sendTransaction(txDataA);
      this.$Modal.info({
        title: "Info",
        content: "approvel all successfully!",
        okText: 'confirm'
      })
      // console.let(sign)
      // let res = await this.web3.eth.sendSignedTransaction(sign.rawTransaction);
      // console.let(res)
    },
    async rubbishClaim() {
      if (this.tokenIdArr.length >= 1) {
        let claimAmount = parseInt(this.tokenIdArr.length / 4);
        this.tokenIdArr = this.tokenIdArr.slice(0, claimAmount * 4);
        let data = await this.beastContract.methods
          .rubbishClaim(this.tokenIdArr)
          .encodeABI();
        let txDataA = {
          // nonce: web3.utils.toHex(nonce),//钱包地址的nonce
          to: this.beastAddress, //这笔交易的接收地址，为router合约
          from: this.address, //交易的发起地址
          data: data, //编码过后的交易数据
          // gas: 500000
          // value:amoutA,//携带的ETH数量
        };
        let res = this.web3.eth.sendTransaction(txDataA);
        this.$Modal.info({
        title: "Info",
        content: "mint beast~~ successfully!",
        okText: 'confirm'
      })
      }
    },
  },
  mounted() {
    this.connectWallet();
    // this.web3 = new this.Web3(
    //   new this.Web3.providers.HttpProvider("https://rinkeby.infura.io/v3/45192e3bb296499a93ef0a73c0eb159a")
    // );
    // console.log(this.web3)
  },
};
</script>

